<template>
  <v-map ref="map" :minZoom="2" :options="mapOptions ? mapOptions : options.map" class="fullHeight">
    <HomeButton />
    <v-geojson v-if="world && world.length > 0" :geojson="world" :options="options.world"></v-geojson>
    <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
  </v-map>
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import HomeButton from './HomeButton'

export default {
  data () {
    return {
      options: {
        map: {
          zoom: 2,
          center: [40, 0],
          maxBounds: [[90, -180], [-60, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillColor: '#fff',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        },
        world: {
          stroke: false,
          fill: true,
          fillColor: '#fff',
          fillOpacity: 0.4,
          interactive: false
        }
      }
    }
  },
  props: {
    geojson: {
      type: Array
    },
    onEachFeature: {
      type: Function
    },
    mapOptions: {
      type: Object
    },
    world: {
      type: Array
    }
  },
  created () {
    this.$parent.$on('zoomCountry', this.zoom)
  },
  methods: {
    zoom (value) {
      this.$refs.map.mapObject.flyTo({...value}, 6)
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    HomeButton
  }
}
</script>


